.main_wrapper {
    display: grid;
    grid-template-columns: 4fr 8fr;
    width: 100%;
    height: 100vh;
    background-color: #0e0e17;
}

.profile_wrapper {

    display: block;
    text-align: center;
    padding-top: 100px;
    margin-left: 50px;
}

.img_wrapper {
    position: relative;
}

.bio_img {
    display: block;
    height: 300px;
    border: 5px solid orange;
    border-radius: 70%;
    z-index: 1;
}

.social {
    z-index: 5;


}

.social > div {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 35px;
    width: 35px;
    margin-right: 30px;
    border: 2px solid whitesmoke;
    border-radius: 50%;
    background-color: #0e0e17;
}

.social > div:hover {
    background-color:  orange ;
    transition-duration: 0.8s;
    transform: scale(1.2);

}


.facebook {
    position: absolute;
    top:250px;
    left:47px;
}

.telegram {
    position: absolute;
    top:275px;
    left: 113px;

}

.mail {
    position: absolute;
    top: 275px;
    left: 185px;
}

.linkedin {
    position: absolute;
    top: 245px;
    left: 248px;
}


.social_icon .telegram {

}

.social a {
    text-align: center;
    box-sizing: border-box;
    font-size: 15px;
    font-weight: 400;
    color: whitesmoke;
}


.name {
    margin-top: 50px;
    padding-right: 35px;
    text-align: center;
    line-height: 1.7;
    font-weight: 300;
}

.name p {
    font-size: 16px;
    color: hsla(0, 0%, 100%, .8)
}

.name h3 {
    font-size: 30px;
    font-weight: 500;
    color: whitesmoke;
}

.bio_wrapper {
    padding-top: 100px;
    padding-left: 80px;
    color: whitesmoke;
    display: inline-block;
    width: 700px;
}

.bio_title {
    font-size: 30px;
    font-weight: 600;
    margin-bottom: 50px;
}


.bio_info {
    font-size: 15px;
    font-weight: 300;
    line-height: 1.7;
    width: 700px;
    color: hsla(0, 0%, 100%, .8);

}

.bio_info > p {
    margin-bottom: 45px;
}

.params_wrapper {
    display: flex;
    justify-content: space-between;
}



.params > ul {
    list-style-type: none;
    font-size: 15px;
    line-height: 2;
}

li> label {
    font-weight: 600;
}

li> span {
    color: hsla(0, 0%, 100%, .8);
}


